(function() {
  'use strict';

  angular
    .module('madBackWeb')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController(moment) {
      var vm = this;

      // "vm.creation" is avaible by directive option "bindToController: true"
      vm.relativeDate = moment(vm.creationDate).fromNow();
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('madBackWeb')
    .controller('navbarCtrl', navbarCtrl)
    .directive('myAccess', myAccess);

  /** @ngInject */
  function navbarCtrl($scope, $location, $state) {
    $scope.isMain = false;
    $scope.isUserManage = false;
    $scope.isAdManage = false;
    $scope.isFinanceManage = false;
    $scope.isAdmin = false;
    $scope.isStatistics = false;

    if ($location.path() == '/') {
      $scope.isMain = true;
    }

    if ($location.path() == '/user/list-mobile' ||
        $location.path() == '/user/list-mobileuser/list-advert' ||
        $location.path() == '/user/list-mobileuser/create') {
      $scope.isUserManage = true;
    }

    if ($location.path() == '/advert/push' ||
        $location.path() == '/advert/audit' ||
        $location.path() == '/advert/search') {
      $scope.isAdManage = true;
    }

    if ($location.path() == '/finance' ||
        $location.path() == '/finance/cash' ||
        $location.path() == '/finance/refund') {
      $scope.isFinanceManage = true;
    }

    if ($location.path() == '/admin/userlist' ||
        $location.path() == '/admin/adduser' ||
        $location.path() == '/admin/auth') {
      $scope.isAdmin = true;
    }

    if ($location.path() == '/statistics') {
      $scope.isStatistics = true;
    }
  }

  function myAccess ($window) {
    return {
      restrict: 'A',
      link: function (scope, element, attributes) {
        var allowedAccess = attributes.myAccess;
        if ($window.localStorage['LEVEL'] === '1') {
          scope.display = 'inline';
        }
        else scope.display = 'none';
      }
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('madBackWeb')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;

          return vm.contributors;
        });
      }
    }

  }

})();

(function () {
  'use strict';

  angular
    .module('madBackWeb')
    .controller('HeaderCtrl', HeaderCtrl);

    /** @ngInject */
    function HeaderCtrl ($scope, $rootScope, $state, $window, MessageSrv, MessageStatusSrv, DeleteMsgSrv, NoticeSrv, ErrorSrv) {
      $scope.logout = function () {
        $window.localStorage.removeItem('token');
        $window.localStorage.removeItem('LEVEL');
        $window.localStorage.removeItem('adminId');
        $state.go('app.login');
        NoticeSrv.notice("您已成功登出");
      }

      $scope.markMsg = function () {
        MessageStatusSrv.changeStatus().save({
          id: $window.localStorage['adminId']
        })
        .$promise.then(
          function (response) {
            console.log(response.errCode);
          }
        ),
        function (error) {
          console.log(error);
        }
      }

      $scope.refresh = function () {
        $state.reload();
      }

      $scope.delete = function (id) {
        DeleteMsgSrv.deleteMsg().save({
          id: id,
          adminId: $window.localStorage['adminId']
        }).$promise.then(
          function (response) {
            NoticeSrv.success("成功删除消息");
            $state.reload();
          },
          function (error) {
            console.log(error);
          }
        )
      }

      MessageSrv.getMessage().get({
        id: $window.localStorage['adminId']
      }).$promise.then(
        function (response) {
          $rootScope.msgList = response.MsgList.reverse();
          var number = 0;
          for (var msg in response.MsgList) {
            if ((response.MsgList)[msg].status == 0) {
              response.MsgList[msg].color = 'text-danger';
              number++;
            } else {
              response.MsgList[msg].color = 'text-info';
            }
          }
          $rootScope.msg = {
            number: number
          }
        }
      ), function (error) {
        console.log(error);
      }
    }

})();

(function() {
  'use strict';

  angular
  .module('madBackWeb')
  .controller('UserListCtrl', UserListCtrl);

  /** @ngInject */
  function UserListCtrl($rootScope, $scope, $state, UserListSrv, GetUserDetailSrv) {
    var tag = 1;
    UserListSrv.getUserInfo().get({
      tag: tag
    }).$promise.then(
      function (response) {
        var list = response.userList;
        for (var user in list) {
          if (list[user].status == '010') {
            list[user].state = '待审核';
            list[user].statusClass = 'label-info';
            list[user].uiSref = 'app.user.check-mobile';
            list[user].operator = '审核用户';
          }
          if (list[user].status == '100') {
             list[user].state = '审核通过';
             list[user].statusClass = 'label-success';
             list[user].uiSref = 'app.user.detail-mobile';
             list[user].operator = '用户详情';
           }
          if (list[user].status == '001') {
            list[user].state = '审核未通过';
            list[user].statusClass = 'label-danger';
            list[user].uiSref = 'app.user.detail-mobile';
            list[user].operator = '用户详情';
          }
        }
        $scope.userList = list;
        $scope.filter = {};

        // $scope.filteredList = []
        // ,$scope.currentPage = 1
        // ,$scope.numPerPage = 5
        // ,$scope.maxSize = 10;
        //
        // $scope.$watch("currentPage + numPerPage", function() {
        //   var begin = (($scope.currentPage - 1) * $scope.numPerPage)
        //   , end = begin + $scope.numPerPage;
        //   $scope.filteredList = $scope.userList.slice(begin, end);
        // });

        $scope.filterByCategory = function (user) {
          return $scope.filter[user.status] || noFilter($scope.filter);
        }
        function noFilter(filterObj) {
          for (var key in filterObj) {
              if (filterObj[key]) {
                  return false;
              }
          }
          return true;
        }
      }, function (error) {
        console.log(error);
      }
    )

    $scope.getDetailById = function (id) {
      GetUserDetailSrv.getUserDetailById().get({
        userid: id,
        tag: tag
      }).$promise.then(
        function (response) {
          $rootScope.mobile = {
            name: response.userDetail.name,
            id: response.userDetail.id,
            location: response.userDetail.location,
            vehicleLicenseImage: response.userDetail.vehicleLicenseImage,
            email: response.userDetail.email,
            mobilePhone: response.userDetail.mobilePhone
          }
        }, function (error) {
          console.log(error);
        }
      )
    }

    $scope.categories = [{
      name: '等待审核',
      labelClass: 'label-info',
      info: '待审核',
      status: '010'
    },{
      name: '审核通过',
      labelClass: 'label-success',
      info: '审核通过',
      status: '100'
    },{
      name: '审核未通过',
      labelClass: 'label-danger',
      info: '审核未通过',
      status: '001'
    }];
  }

})();

(function() {
  'use strict';

  angular
  .module('madBackWeb')
  .controller('AdvertListCtrl', AdvertListCtrl);

  /** @ngInject */
  function AdvertListCtrl($rootScope, $scope, $state, UserListSrv, GetUserDetailSrv) {
    var tag = 2;
    UserListSrv.getUserInfo().get({
      tag: tag
    }).$promise.then(
      function (response) {
        var list = response.advertiserList;
        for (var user in list) {
          if (list[user].status == '010') {
            list[user].state = '待审核';
            list[user].statusClass = 'label-info';
            list[user].uiSref = 'app.user.check-advert';
            list[user].operator = '审核广告商';
          }
          if (list[user].status == '100') {
            list[user].state = '审核通过';
             list[user].statusClass = 'label-success';
             list[user].uiSref = 'app.user.detail-advert';
             list[user].operator = '广告商详情';
           }
          if (list[user].status == '001') {
            list[user].state = '审核未通过';
            list[user].statusClass = 'label-danger';
            list[user].uiSref = 'app.user.detail-advert';
            list[user].operator = '广告商详情';
          }
        }
        $scope.userList = list;
        $scope.filter = {};
        $scope.filterByCategory = function (user) {
          return $scope.filter[user.status] || noFilter($scope.filter);
        }
        function noFilter(filterObj) {
          for (var key in filterObj) {
              if (filterObj[key]) {
                  return false;
              }
          }
          return true;
        }
      }, function (error) {
        console.log(error);
      }
    )

    $scope.getDetailById = function (id) {
      GetUserDetailSrv.getUserDetailById().get({
        userid: id,
        tag: tag
      }).$promise.then(
        function (response) {
          $rootScope.advert = {
            name: response.userDetail.name,
            id: response.userDetail.id,
            status: response.userDetail.status,
            email: response.userDetail.email,
            registerDate: response.userDetail.registerDate,
            type: response.userDetail.detail.type,
            licenseType: response.userDetail.detail.licenseType,
            licenseImage: response.userDetail.detail.licenseImage,
            licenseCode: response.userDetail.detail.licenseCode,
            location: response.userDetail.detail.location,
            accomodation: response.userDetail.detail.accomodation,
            businessScope: response.userDetail.detail.businessScope,
            businessPeriod: response.userDetail.detail.businessPeriod,
            organizationCode: response.userDetail.detail.organizationCode,
            legalPersonName: response.userDetail.detail.legalPerson.name,
            legalPersonLocation: response.userDetail.detail.legalPerson.location,
            legalPersonId: response.userDetail.detail.legalPerson.id,
            legalPersonValidDate: response.userDetail.detail.legalPerson.validDate,
            legalPersonIfLongTerm: response.userDetail.detail.legalPerson.ifLongTerm,
            legalPersonIfLegalPerson: response.userDetail.detail.legalPerson.ifLegalPerson,
          }
          console.log(response);
        }, function (error) {
          console.log(error);
        }
      )
    }

    $scope.categories = [{
      name: '等待审核',
      labelClass: 'label-info',
      info: '待审核',
      status: '010'
    },{
      name: '审核通过',
      labelClass: 'label-success',
      info: '审核通过',
      status: '100'
    },{
      name: '审核未通过',
      labelClass: 'label-danger',
      info: '审核未通过',
      status: '001'
    }];

  }

})();




(function () {
  'use strict';

  angular
  .module('madBackWeb')
  .directive('modal', modal)
  .controller('UserCheckCtrl', UserCheckCtrl);

  function UserCheckCtrl ($rootScope, $scope, $state, CheckUserSrv, NoticeSrv, ErrorSrv) {
    $scope.checkSuccess = function (id, tag) {
      var reason = '';
      CheckUserSrv.checkUser().save({
        id: id,
        tag: tag,
        success: 1,
        reason: reason
      }).$promise.then(
        function (response) {
          NoticeSrv.success("操作成功");
          if (tag == 1) $state.go('app.user');
          if (tag == 2) $state.go('app.user.list-advert');
        },
        function (error) {
          NoticeSrv.notice("操作失败");
          console.log(error);
        }
      )
    }

    $scope.checkFailure = function (id, tag, reason) {
      if (!reason) {
        NoticeSrv.error(ErrorSrv.getError('409'));
      } else {
        CheckUserSrv.checkUser().save({
          id: id,
          tag: tag,
          success: 0,
          reason: reason
        }).$promise.then(
          function (response) {
            NoticeSrv.success("操作成功");
          },
          function (error) {
            NoticeSrv.notice("操作失败");
            console.log(error);
          }
        )
      }
    }

  }

  function modal ($location, $state) {
    return {
      restrict: 'AE',
      require: 'ngModel',
      link: function (scope, element, attrs, ngModel) {
        element.bind("click", function() {
          var obj = $("#exampleModal");
          obj.modal('hide');
          scope.$apply(function(){
            scope.back = true;
            scope.pass = true;
            scope.notPass = true;
          })
        });
      }
    }
  }

})();


